.nav {
    grid-area: nav;
    background-color: #141414;
    padding: 25px;
    margin-left: 10px;
    display: block;
    max-width: 200px;
}

.nav div {
    position: relative;
    margin-bottom: 10px;
    width: 230px;
    height: 60px;
}

.navLink {
    display: block;
    max-width: 230px;
    height: 20px;
    padding-left: 50px;
    padding-top: 20px;
    padding-bottom: 20px;
    border-radius: 5px;
    text-decoration: none;
}

.navLink:hover {
    background-color: #3b3b3b;
    transition: 500ms background-color;
}

.icon {
    position: absolute;
    width: 25px;
    top: 16px;
    left: 15px;
}

@media (max-width: 770px) {
    .nav {
        padding: 25px 0 0 0;
        margin-left: 0;
        max-width: 100px;
    }

    .nav div {
        margin-left: 30px;
        width: 100px;
    }

    .navLink {
        padding-left: 10px;
        max-width: 100px;
    }

    .icon {
        display: none;
    }
}